@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
		'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.app__wrapper{position:relative;width:960px;height:1350px;margin:0 auto;background:#f3f7fa}.main{position:absolute;width:503px;height:50px;left:355px;top:160px}.filter{position:absolute;height:252px;width:232px;left:103px;top:160px;background:#ffffff;box-shadow:0px 2px 8px rgba(0,0,0,0.1);border-radius:5px;padding:20px !important}.tab{width:50%;height:50px;left:calc(50% - 252px / 2 + 1px);top:160px;display:flex;align-items:center;justify-content:center;cursor:pointer;background:white;color:black}.tab.active{background:#2196f3;color:white}.tab .inner{width:138px;height:19px;left:calc(50% - 138px / 2 + 1px);top:175px;font-family:Open Sans;font-style:normal;font-weight:600;font-size:12px;line-height:20px;text-align:center;letter-spacing:0.5px;text-transform:uppercase}.ticket{width:502px;height:184px;margin-top:20px !important;padding:20px 0 0 20px;background:#ffffff;box-shadow:0px 2px 8px rgba(0,0,0,0.1);border-radius:5px;transition-duration:0.3s}.ticket .header{height:36px;left:375px;top:250px;margin-bottom:20px;display:flex}.ticket .header .price{font-family:Open Sans;font-style:normal;font-weight:600;font-size:24px;line-height:24px;display:flex;align-items:center;color:#2196f3}.ticket .header .logo-s7{text-align:right;right:50px}.ticket .row1{height:39px;margin-bottom:10px;display:flex}.ticket .row2{height:39px;display:flex}.ticket:hover{cursor:pointer;box-shadow:0px 2px 8px rgba(0,0,0,0.25)}.header-left,.header-center,.header-right{font-family:Open Sans;font-style:normal;font-weight:600;font-size:12px;line-height:18px;display:flex;align-items:center;letter-spacing:0.5px;text-transform:uppercase;color:#a0b0b9}.body-left,.body-center,.body-right{font-family:Open Sans;font-style:normal;font-weight:600;font-size:14px;line-height:21px;display:flex;align-items:center;color:#4a4a4a}.count ul{width:100%;list-style:none;font-family:Open Sans;font-style:normal;font-weight:normal;font-size:13px;line-height:20px}.count ul li{height:40px;display:flex;align-items:center}.count ul li:hover{background:#f1fcff;cursor:pointer}.count ul li div{display:inline-flex}.count ul li input{margin:0 10px 0 20px}.count input{border:1px solid #2196f3;box-sizing:border-box;border-radius:2px}.count ul label{width:100%}.count label input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.checkmark{position:relative;height:20px;width:20px;border:1px solid #9abbce;box-sizing:border-box;border-radius:2px;margin:0 10px 0 20px}.count label input:checked ~ .checkmark{border:1px solid #2196f3;box-sizing:border-box;border-radius:2px}.checkmark:after{content:'';position:absolute;display:none}.count label input:checked ~ .checkmark:after{display:block}.count label .checkmark:after{position:absolute;left:6px;top:3px;width:6px;height:10px;border:solid #2196f3;border-width:0 2px 2px 0;transform:rotate(45deg)}.logo{text-align:center;padding-top:40px}body{background:#f3f7fa}.row,.col-3,.col-4,.col-6,.col-9,ul,li,label{margin:0 !important;padding:0 !important}

